(() => {

  const getCookie = name => {
    let end;
    const dc = document.cookie;
    const prefix = `${name  }=`;
    let begin = dc.indexOf(`; ${  prefix}`);
    if (begin == - 1) {
      begin = dc.indexOf(prefix);
      if (begin != 0) return null;
    }
    else
    {
      begin += 2;
      end = document.cookie.indexOf(`;`, begin);
      if (end == - 1) {
        end = dc.length;
      }
    }
    // because unescape has been deprecated, replaced with decodeURI
    //return unescape(dc.substring(begin + prefix.length, end));
    return decodeURI(dc.substring(begin + prefix.length, end));
  };


  const onBottomFilterClick = () => {

    document.cookie = `state=true;max-age=${  60 * 5}`;

  };

  const setFilter = () => {

    console.log(`succes`);
    const $searchContainer = document.querySelector(`.filter-search-container`);
    const $filterBottom = document.querySelector(`.filter-bottom`);
    const $filterItems = document.querySelector(`.filter-items`);
    const $filterLink = document.querySelector(`.filter-link`);


    $searchContainer.classList.add(`hide`);
    $filterBottom.classList.remove(`hide`);
    $filterItems.classList.remove(`hide`);
    if ($filterItems.classList.contains(`hide`)) {
      $filterLink.innerHTML = `Filter`;
    } else {
      $filterLink.innerHTML = `X`;
    }

  };


  const onFilterClick = () => {

    // console.log(`succes`);
    const $searchContainer = document.querySelector(`.filter-search-container`);
    const $filterBottom = document.querySelector(`.filter-bottom`);
    const $filterItems = document.querySelector(`.filter-items`);
    const $filterLink = document.querySelector(`.filter-link`);

    $searchContainer.classList.toggle(`hide`);
    $filterBottom.classList.toggle(`hide`);
    $filterItems.classList.toggle(`hide`);
    if ($filterItems.classList.contains(`hide`)) {
      $filterLink.innerHTML = `Filter`;
    } else {
      $filterLink.innerHTML = `X`;
    }

  };

  const init = () => {
    document.querySelector(`.filter-button`).addEventListener(`click`, onFilterClick);
    document.querySelector(`.filter-bottom-button`).addEventListener(`click`, onBottomFilterClick);
    const $myCookie = getCookie(`state`);
    if ($myCookie == null) {
      //
    } else {
      setFilter();
      document.cookie = `state=true;max-age=${1}`;
      // alert('gelukt');
    }
  };


  init();

})();



